___CSS_LOADER_EXPORT___.push([module.id, `:root {
    --title-font-size: 1.4rem;
    --noral-font-size: 1rem;
    --input-height: 2rem;
    --input-width: 150px;

    --sidebar-clr: #262626;
    --project-clr: #1e1e1e;
    --font-clr: #fff;
    --dropdown-clr: #303030;

    --task-width: 600px;

    /*Colors representing priorities*/
    --priority-brdr-clr: #d7d6d6;
    --priority-bg-clr: #e1dfdf3f;
}

dialog.task-form-modal {
    padding: 0;
    border: none;
    width: 100%;
    height: 100vh;
    background: none;

    &::backdrop {
        background: #ffffff0a;
    }

    max-height: 100%;
    min-width: 100%;
    position: static;
}  

form.task-form {
    width: var(--task-width);
    margin-top: 120px;
}

form.task-form,
form.task-editor {
    margin-inline: auto;
    padding: 1.3rem 1.1rem;

    color: var(--font-clr);
    background-color: var(--sidebar-clr);
    font-size: var(--noral-font-size);

    border: 1px solid #4d4d4d2e;
    border-radius: 5px;

    box-shadow: 0 0 3px 0px var(--grey-font-clr);
}

.task-wrapper:has(form.task-editor) {
    display: block;
}

.task-form input[type="text"],
.task-editor input[type="text"] {
    background-color: transparent;
    &:user-invalid {
        border: 1px solid #983a3a;
    }
    width: 100%;
}

input:-webkit-autofill,
input:-webkit-autofill:hover, 
input:-webkit-autofill:focus, 
input:-webkit-autofill:active{
    -webkit-background-clip: text;
    -webkit-text-fill-color: var(--font-clr);
    transition: background-color 5000s ease-in-out 0s;
    box-shadow: inset 0 0 20px 20px transparent;
}

.task-form input,
.task-form select,
.task-form option,
.task-editor input,
.task-editor select,
.task-editor option {
    border: none;
    outline: none;
    color: #ededed;
    border-radius: 4px;
}

.task-form #task-description,
.task-editor #task-description-edit {
    color: var(--grey-font-clr);
}


select#task-priority,
select#task-priority-edit {
    color: var(--priority-bg-clr);

    &:has(option:checked[value="0"]) {
        --priority-bg-clr: #d7d6d6;
    }

    &:has(option:checked[value="1"]) {
        --priority-bg-clr: #5189d1;
    }

    &:has(option:checked[value="2"]) {
        --priority-bg-clr: #d1c251;
    }

    &:has(option:checked[value="3"]) {
        --priority-bg-clr: #d15151;
    }
}

select#task-priority > option,
select#task-priority-edit > option {
    color: var(--priority-bg-clr);

    &[value="0"] {
        --priority-bg-clr: #d7d6d6;
    }

    &[value="1"] {
        --priority-bg-clr: #5189d1;
    }

    &[value="2"] {
        --priority-bg-clr: #d1c251;
    }

    &[value="3"] {
        --priority-bg-clr: #d15151;
    }
}

.task-form .multi-inputs,
.task-editor .multi-inputs {
    margin-top: 1rem;
    display: flex;
    gap: 24px;
}

.task-form select,
.task-form input[type="date"],
.task-editor select,
.task-editor input[type="date"] {
    background-color: var(--dropdown-clr);
    height: var(--input-height);
    width: var(--input-width);
    padding-left: 0.6rem;
}

input#task-title,
input#task-title-edit {
    font-weight: 900;
    font-size: var(--title-font-size);
}

.task-form > .form-row:last-of-type,
.task-editor > .form-row:last-of-type {
    margin-top: 1.4rem;
    padding-top: 1rem;
    border-top: 1px solid var(--grey-font-clr);

    display: flex;
    justify-content: space-between;
    gap: 8px;

    & select {
        margin-left: 0;
        margin-right: auto;
    }
}

.task-form button,
.task-editor button {
    border: none;
    outline: none;
    width: 85px;
    height: var(--input-height);
    border-radius: 2px;
    color: #ffffff;

    line-height: 1;

    &.cancel-task {
        background-color: #474747;
    }
    &.add-task,
    &.save-task {
        background-color: #873232;
    }
}

@media only screen and (max-width: 800px) {
    form.task-form {
        --task-width: 560px;
        margin-top: 100px;
    }
}

@media only screen and (max-width: 700px) {
    form.task-form {
        --task-width: 500px;
    }
}

@media only screen and (max-width: 600px) {
    form.task-form {
        --task-width: 450px;
        padding-block: 1rem;
    }
}

@media only screen and (max-width: 500px) {
    form.task-form {
        --task-width: 400px;
    }

    form.task-editor select,
    form.task-editor input[type="date"] {
        font-size: 0.9rem;
        padding-top: 4px;
        width: 140px;
    }

    form.task-editor button {
        width: 60px;
        font-size: 0.9rem;
    }
}

@media only screen and (max-width: 430px) {
    form.task-form {
        --task-width: 350px;
    }

    form.task-form button {
        width: 70px;
        font-size: 0.9rem;
    }

    form.task-form .multi-inputs,
    .task-form > .form-row:last-of-type,
    form.task-editor .multi-inputs {
        justify-content: space-between;
        & > select {
            margin: 0;
        }
    }

    form.task-editor .multi-inputs {
        flex-direction: column;
        gap: 12px;

        & > select,
        & > input[type="date"] {
            width: 100%;
        }
    }

    .task-editor > .form-row:last-of-type {
        & > select {
            width: 100px;
        }
    }

    form.task-form select,
    form.task-form input[type="date"] {
        width: 145px;
        font-size: 0.9rem;
        padding-top: 4px;
    }
}`, "",{"version":3,"sources":["webpack://./src/styles/task-form-editor.css"],"names":[],"mappings":"AAAA;IACI,yBAAyB;IACzB,uBAAuB;IACvB,oBAAoB;IACpB,oBAAoB;;IAEpB,sBAAsB;IACtB,sBAAsB;IACtB,gBAAgB;IAChB,uBAAuB;;IAEvB,mBAAmB;;IAEnB,iCAAiC;IACjC,4BAA4B;IAC5B,4BAA4B;AAChC;;AAEA;IACI,UAAU;IACV,YAAY;IACZ,WAAW;IACX,aAAa;IACb,gBAAgB;;IAEhB;QACI,qBAAqB;IACzB;;IAEA,gBAAgB;IAChB,eAAe;IACf,gBAAgB;AACpB;;AAEA;IACI,wBAAwB;IACxB,iBAAiB;AACrB;;AAEA;;IAEI,mBAAmB;IACnB,sBAAsB;;IAEtB,sBAAsB;IACtB,oCAAoC;IACpC,iCAAiC;;IAEjC,2BAA2B;IAC3B,kBAAkB;;IAElB,4CAA4C;AAChD;;AAEA;IACI,cAAc;AAClB;;AAEA;;IAEI,6BAA6B;IAC7B;QACI,yBAAyB;IAC7B;IACA,WAAW;AACf;;AAEA;;;;IAII,6BAA6B;IAC7B,wCAAwC;IACxC,iDAAiD;IACjD,2CAA2C;AAC/C;;AAEA;;;;;;IAMI,YAAY;IACZ,aAAa;IACb,cAAc;IACd,kBAAkB;AACtB;;AAEA;;IAEI,2BAA2B;AAC/B;;;AAGA;;IAEI,6BAA6B;;IAE7B;QACI,0BAA0B;IAC9B;;IAEA;QACI,0BAA0B;IAC9B;;IAEA;QACI,0BAA0B;IAC9B;;IAEA;QACI,0BAA0B;IAC9B;AACJ;;AAEA;;IAEI,6BAA6B;;IAE7B;QACI,0BAA0B;IAC9B;;IAEA;QACI,0BAA0B;IAC9B;;IAEA;QACI,0BAA0B;IAC9B;;IAEA;QACI,0BAA0B;IAC9B;AACJ;;AAEA;;IAEI,gBAAgB;IAChB,aAAa;IACb,SAAS;AACb;;AAEA;;;;IAII,qCAAqC;IACrC,2BAA2B;IAC3B,yBAAyB;IACzB,oBAAoB;AACxB;;AAEA;;IAEI,gBAAgB;IAChB,iCAAiC;AACrC;;AAEA;;IAEI,kBAAkB;IAClB,iBAAiB;IACjB,0CAA0C;;IAE1C,aAAa;IACb,8BAA8B;IAC9B,QAAQ;;IAER;QACI,cAAc;QACd,kBAAkB;IACtB;AACJ;;AAEA;;IAEI,YAAY;IACZ,aAAa;IACb,WAAW;IACX,2BAA2B;IAC3B,kBAAkB;IAClB,cAAc;;IAEd,cAAc;;IAEd;QACI,yBAAyB;IAC7B;IACA;;QAEI,yBAAyB;IAC7B;AACJ;;AAEA;IACI;QACI,mBAAmB;QACnB,iBAAiB;IACrB;AACJ;;AAEA;IACI;QACI,mBAAmB;IACvB;AACJ;;AAEA;IACI;QACI,mBAAmB;QACnB,mBAAmB;IACvB;AACJ;;AAEA;IACI;QACI,mBAAmB;IACvB;;IAEA;;QAEI,iBAAiB;QACjB,gBAAgB;QAChB,YAAY;IAChB;;IAEA;QACI,WAAW;QACX,iBAAiB;IACrB;AACJ;;AAEA;IACI;QACI,mBAAmB;IACvB;;IAEA;QACI,WAAW;QACX,iBAAiB;IACrB;;IAEA;;;QAGI,8BAA8B;QAC9B;YACI,SAAS;QACb;IACJ;;IAEA;QACI,sBAAsB;QACtB,SAAS;;QAET;;YAEI,WAAW;QACf;IACJ;;IAEA;QACI;YACI,YAAY;QAChB;IACJ;;IAEA;;QAEI,YAAY;QACZ,iBAAiB;QACjB,gBAAgB;IACpB;AACJ","sourcesContent":[":root {\n    --title-font-size: 1.4rem;\n    --noral-fon