___CSS_LOADER_EXPORT___.push([module.id, `:root {
    --sidebar-clr: #262626;
    --project-clr: #1e1e1e;
    --font-clr: #fff;
    --grey-font-clr: #afafaf;

    --task-width: 600px;

    /*Colors representing priorities*/
    --priority-brdr-clr: #d7d6d6;
    --priority-bg-clr: #e1dfdf3f;

    /*Sidebar*/
    --sidebar-width: 300px;
}

body {
    min-width: 375px;
    display: grid;
    grid-template-columns: auto 1fr;
    grid-template-areas: "sidebar-panel project-panel";

    background-color: var(--project-clr);

    color: var(--font-clr);
    font-family: Roboto, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Apple Color Emoji", Helvetica, Arial, sans-serif, "Segoe UI Emoji", "Segoe UI Symbol";
    letter-spacing: 1px;
}

nav.sidebar-panel {
    grid-area: sidebar-panel;

    background-color: var(--sidebar-clr);

    width: 300px;
    height: 100lvh;
    left: 0;
    position: fixed;
    z-index: 1;

    transition: width 0.5s 1ms,
                left 0.5s ease-in;

    > * {
        opacity: 1;
        transition: opacity 0.2s;
    }
}

main.project-panel {
    grid-area: project-panel;
    height: 100%;
    background-color: var(--project-clr);

    margin-left: 300px;
    padding: 2.4rem 50px;

    display: flex;
    flex-direction: column;
    align-items: center;

    transition: margin-left 0.5s;

    & > * {
        width: min(100%, var(--task-width));
        min-width: 210px;
    }

}

.project-panel .title-row {
    display: flex;
    justify-content: space-between;

    & > button.show-task-form {
        width: auto;
    }
}

/*Task Wrapper*/
.task-wrapper {
    border-bottom: 1px solid #ececec7a;
    margin-top: 1.1rem;
    padding-bottom: 0.5rem;

    display: grid;
    grid-template-columns: 1rem 1fr 1.2rem;
}

.task-wrapper > .info-wrapper {
    margin-left: 1rem;
}

h3.task-title {
    font-size: 1.15rem;
    line-height: 1;
}

p.task-description {
    font-size: 0.95rem;
    color: var(--grey-font-clr);
    min-height: 1rem;
    line-height: 1.2;
    margin-top: 6px;
    margin-bottom: 5px;
}

span.task-dueDate {
    font-size: 0.95rem;
    color: #d4d4d4;
}

/*Task complete button*/
button.task-complete {
    appearance: none;
    outline: none;
    border: none;

    aspect-ratio: 1/1;
    border-radius: 50%;
    border: 2.4px solid var(--priority-brdr-clr);
    background-color: var(--priority-bg-clr);

    position: relative;

    &::before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
        background-position: center;
        background-size: contain;
        background-repeat: no-repeat;

        opacity: 0;
        transition: opacity 0.1s ease-in;
    }

    &:hover::before {
        opacity: 1;
    }
}

/*Priority options*/
.task-wrapper.priority-1 > button.task-complete {
    --priority-brdr-clr: #5189d1;
    --priority-3-bg-clr: #4cb8e33f;
}

.task-wrapper.priority-2 > button.task-complete {
    --priority-brdr-clr: #d1c251;
    --priority-bg-clr: #e3ca4c3f;
}

.task-wrapper.priority-3 > button.task-complete {
    --priority-brdr-clr: #d15151;
    --priority-bg-clr: #e34c4c3f;
}

/*Task Editor button*/
button.task-edit {
    appearance: none;
    outline: none;
    border: none;
    
    aspect-ratio: 1/1;

    background: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
}


@media only screen and (max-width: 800px) {
     body nav.sidebar-panel {
        width: 250px;
        padding-top: 2.4rem;
    }

    .sidebar-panel > div.first-row {
        align-items: start;
        & > h1 {
            font-size: 1.6rem;
        }

        & button.sidebar-toggle {
            width: 35px;
        }
    }

    main.project-panel {
        margin-left: 250px;
        padding-inline: 40px;
    }
}

@media only screen and (max-width: 700px) {
    body nav.sidebar-panel {
        width: 200px;
    }

    .sidebar-panel > div.first-row {
        & h1 {
            font-size: 1.4rem;
        }
    }

    main.project-panel {
        margin-left: 200px;
        padding-inline: 30px;
    }

    .project-panel .title-row > h1 {
        font-size: 1.5rem;
    }
}

@media only screen and (max-width: 555px) {
    body nav.sidebar-panel {
        width: 0px;
        left: -300px;
        position: absolute;

        & > * {
            opacity: 0;
            width: 100%;
        }
    }

    body main.project-panel {
        margin-left: 0;
    }

    .sidebar-panel > div.first-row {
        & h1 {
            font-size: 1.4rem;
        }
    }

    main div.first-row button.sidebar-toggle.hidden {
        display: block;
    }

    .project-panel .title-row > button.show-task-form {
        font-size: 1.1rem;
        line-height: 1.2;
        &::before {
            width: 1.6rem;
        }
    }
